* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
}

.image-slider-frame {
  height: 400px;
  width: 600px;
  border: 1px solid #a6a6a6;
  border-radius: 12px;
  justify-content: space-around;
  align-items: center;
  margin: 2rem auto;
  display: flex;
  position: relative;
}

.image-slider-frame .image-container {
  height: 75%;
  width: 80%;
  border-radius: 12px;
  overflow: hidden;
}

.image-slider-frame .image-container img.slider-img {
  width: 100%;
}

.image-slider-frame .left-arrow-div, .image-slider-frame .right-arrow-div {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}

.image-slider-frame .left-arrow-div svg, .image-slider-frame .right-arrow-div svg {
  pointer-events: none;
}

.image-slider-frame .nav-dot-container {
  height: 10%;
  width: 50%;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: 0;
}

.image-slider-frame .nav-dot-container .nav-dot {
  height: 1rem;
  width: 1rem;
  cursor: pointer;
  border: 1px solid #a6a6a6;
  border-radius: 50%;
}

.image-slider-frame .nav-dot-container .nav-dot.filled {
  background-color: #a6a6a6;
}

.flex-center {
  justify-content: center;
  align-items: center;
  display: flex;
}

